*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Roboto Slab', serif;
}

.navbar{
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 1.9rem !important;
    color: #565387 !important;
}
.navbar-brand img{
    width: 150px !important;
    height: 60px !important;
}

.navbar a{
    font-size: 1.3 rem !important;
    text-transform: capitalize;
    color: black !important;
}
.active-menu{
    font-style: bold !important;
    border-bottom: 2px solid #3498db !important;
}
.navbar a:hover{
    color: #3498db !important;
}
.data-table{
    background-color: #ffff !important;
    box-shadow: 0px 3px 21px 0px rgba(0,0,0,0.75);
}



/* =====================Header================= */

.main-header{
    height: 80vh;
}
.image-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.image-container img{
    width: 300px;
    height: 300px;
    -webkit-animation: heartBeat 5s linear infinite;
            animation: heartBeat 5s linear infinite;
}

.title-container {
    display: flex;
    justify-content: center;
    align-items: center;
}
.title-container h1 span img{
    width: 50px;
    height: 50px;
    -webkit-animation: rotateCorona 2s linear infinite;
            animation: rotateCorona 2s linear infinite;
}

@-webkit-keyframes heartBeat{
    0%{transform: scale(0.75);}
    20%{transform: scale(1);}
    40%{transform: scale(0.75);}
    60%{transform: scale(1);}
    80%{transform: scale(0.75);}
   100%{transform: scale(0.75);}
}

@keyframes heartBeat{
    0%{transform: scale(0.75);}
    20%{transform: scale(1);}
    40%{transform: scale(0.75);}
    60%{transform: scale(1);}
    80%{transform: scale(0.75);}
   100%{transform: scale(0.75);}
}
@-webkit-keyframes rotateCorona{
    0%{transform: rotate(0);}
    100%{transform: rotate(360deg);}
}
@keyframes rotateCorona{
    0%{transform: rotate(0);}
    100%{transform: rotate(360deg);}
}

